*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-color: #2196f3;
  --title-color: #212121;
  --desc-color: #757575;
  --dark-theme-color: #fff;
  --sprite-color: #afb1b8;
  --light-back-color: #f5f4fa;
  --dark-back-color: #2f303a;
  --primary-font: "Roboto", sans-serif;
  --secondary-font: "Raleway", sans-serif;
  --primary-transform: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h1, h2, h3, h4, h5, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  height: auto;
  width: 100%;
  display: block;
}

.link-none {
  text-decoration: none;
}

.list-none {
  list-style: none;
}

button {
  cursor: pointer;
}

body {
  font-family: var(--primary-font);
  background-color: var(--dark-theme-color);
}

.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.section__title {
  color: var(--title-color);
  text-align: center;
  letter-spacing: .03em;
  margin-top: 0;
  margin-bottom: 30px;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.17857;
}

@media screen and (min-width: 1200px) {
  .section {
    padding-top: 94px;
    padding-bottom: 94px;
  }

  .section__title {
    text-align: center;
    letter-spacing: .03em;
    margin-top: 0;
    margin-bottom: 50px;
    font-size: 36px;
    font-weight: 700;
    line-height: 1.16667;
  }
}

.backdrop {
  width: 100vw;
  height: 100vh;
  transition: transform var(--primary-transform);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(0);
}

.studio-button {
  width: 200px;
  height: 50px;
  background-color: var(--primary-color);
  color: var(--dark-theme-color);
  letter-spacing: .06em;
  white-space: nowrap;
  transition: background-color var(--primary-transform);
  border: none;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.875;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.studio-button:hover, .studio-button:focus {
  background-color: #188ce8;
}

.modal-window {
  width: 100%;
  height: 100%;
  max-width: 450px;
  max-height: 609px;
  background-color: var(--dark-theme-color);
  border-radius: 4px;
  padding: 40px;
  position: relative;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal-window__close-btn {
  width: 30px;
  height: 30px;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-window__close-icon {
  transition: fill var(--primary-transform);
}

.modal-window__close-btn:hover .modal-window__close-icon, .modal-window__close-btn:focus .modal-window__close-icon {
  fill: var(--primary-color);
}

.modal-window__title {
  max-width: 370px;
  color: var(--title-color);
  text-align: center;
  letter-spacing: .03em;
  margin-bottom: 12px;
  margin-left: auto;
  margin-right: auto;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.5;
}

@media screen and (min-width: 1200px) {
  .modal-window {
    max-width: 528px;
    max-height: 590px;
  }

  .modal-window__title {
    max-width: 450px;
  }
}

.modal-form {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  row-gap: 10px;
  display: flex;
  position: relative;
}

.modal-form__label {
  cursor: pointer;
  width: 100%;
  color: var(--desc-color);
  letter-spacing: .01em;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
  position: relative;
}

.modal-form__field {
  width: 100%;
  height: 40px;
  transition: outline var(--primary-transform);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  margin-top: 4px;
  padding: 14px 5px 14px 42px;
  display: block;
}

.modal-form__field:focus {
  outline: 2px solid var(--primary-color);
}

.modal-form__textarea {
  width: 100%;
  height: 120px;
  resize: none;
  transition: outline var(--primary-transform);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  margin-top: 4px;
  padding: 12px 16px;
  display: block;
}

.modal-form__textarea:focus {
  outline: 2px solid var(--primary-color);
}

.modal-form__field-icon {
  transition: fill var(--primary-transform);
  display: block;
  position: absolute;
  top: 50%;
  left: 12px;
}

.modal-form__field:hover + .modal-form__field-icon, .modal-form__field:focus + .modal-form__field-icon {
  fill: var(--primary-color);
}

.modal-form__policy {
  cursor: pointer;
  width: 100%;
  color: var(--desc-color);
  letter-spacing: .03em;
  text-align: center;
  justify-content: center;
  align-items: center;
  column-gap: 4px;
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
  display: flex;
}

.modal-form__policy:before {
  content: "";
  width: 16px;
  height: 15px;
  border: 2px solid var(--title-color);
  transition: background-image var(--primary-transform), border var(--primary-transform);
  border-radius: 2px;
  margin-right: 4px;
  display: inline-block;
}

.modal-form__checkbox:checked + .modal-form__policy:before {
  background-image: url("icon-check.606c8314.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: none;
}

.modal-form__policy-link {
  color: var(--primary-color);
}

@media screen and (min-width: 1200px) {
  .modal-form__policy {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.71429;
  }
}

.logo {
  color: #000;
  font-family: var(--secondary-font);
  letter-spacing: .03em;
  cursor: pointer;
  transition: color var(--primary-transform);
  font-size: 24px;
  font-weight: 700;
  line-height: 1.16667;
}

.logo.dark-theme {
  color: var(--dark-theme-color);
}

.logo:hover {
  color: var(--primary-color);
}

.logo__accent {
  color: var(--primary-color);
  transition: color var(--primary-transform);
}

.logo:hover > .logo__accent {
  color: #000;
}

.logo:hover > .logo__accent.dark-theme {
  color: var(--dark-theme-color);
}

.mobile-block {
  display: none;
}

@media screen and (max-width: 768px) {
  .mobile-block {
    width: 100vw;
    height: 100vh;
    z-index: 100;
    background-color: var(--dark-theme-color);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    display: block;
    position: fixed;
    top: 0;
    left: 0;
  }

  .mobile-block.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .mobile-block__container {
    height: 100%;
    letter-spacing: .02em;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 48px;
    padding-bottom: 48px;
    padding-left: 40px;
    display: flex;
    position: relative;
  }

  .mobile-block__close-btn {
    cursor: pointer;
    z-index: 99;
    position: absolute;
    top: 10px;
    right: 15px;
  }

  .mobile-block__bottom {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    row-gap: 64px;
    display: flex;
  }

  .mobile-block__menu, .mobile-block__address {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    row-gap: 32px;
    display: flex;
  }

  .mobile-block__link {
    color: var(--title-color);
    font-size: 40px;
    font-weight: 500;
    line-height: 1.175;
  }

  .mobile-block__tel-link {
    color: var(--title-color);
    font-size: 34px;
    font-weight: 500;
    line-height: 1.17647;
  }

  .mobile-block__mail-link {
    color: var(--desc-color);
    font-size: 24px;
    font-weight: 500;
    line-height: 1.16667;
  }

  .mobile-block__links {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    row-gap: 5px;
    display: flex;
  }

  .mobile-block__links-item {
    color: var(--primary-color);
    font-size: 18px;
    font-weight: 500;
    line-height: 1.22222;
  }

  .mobile-block__links-item:not(:last-child) {
    border-right: 1px solid rgba(33, 33, 33, .2);
    padding-right: 10px;
  }

  .mobile-block__links-item:not(:first-child) {
    padding-left: 10px;
  }

  .mobile-block__links-link {
    color: var(--primary-color);
  }
}

@media screen and (max-height: 520px) {
  .mobile-block__menu, .mobile-block__address {
    row-gap: 16px;
  }

  .mobile-block__bottom {
    row-gap: 32px;
  }
}

@media screen and (max-height: 450px) {
  .mobile-block__menu, .mobile-block__address {
    row-gap: 10px;
  }

  .mobile-block__bottom {
    row-gap: 20px;
  }
}

.page-header {
  height: 80px;
  border-style: solid;
  border-color: #ececec;
  border-bottom-width: 1px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14286;
}

.page-header__container {
