  }
}

@media screen and (min-width: 768px) {
  .site-nav {
    display: block;
  }

  .burger {
    display: none;
  }

  .page-header__left {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .page-header__logo {
    margin-right: 88px;
  }

  .site-nav__list {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .site-nav__item {
    position: relative;
  }

  .site-nav__item:not(:last-child) {
    margin-right: 50px;
  }

  .site-nav__link {
    color: var(--title-color);
    letter-spacing: .02em;
    transition: color var(--primary-transform);
  }

  .site-nav__link:hover {
    color: var(--primary-color);
  }

  .page-header__address {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    row-gap: 10px;
    display: flex;
  }

  .address__link {
    color: var(--desc-color);
    transition: color var(--primary-transform);
    justify-content: flex-start;
    align-items: center;
    font-style: normal;
    display: flex;
  }

  .address__link:hover {
    color: var(--primary-color);
  }

  .address__sprite {
    fill: var(--desc-color);
    transition: fill var(--primary-transform);
    margin-right: 8px;
  }

  .address__link:hover > .address__sprite {
    fill: var(--primary-color);
